@mixin lp {
	@media only screen and (max-width: "1850px") {
		@content; } }
@mixin ld {
	@media only screen and (max-width: "1649px") {
		@content; } }
@mixin lg {
	@media only screen and (max-width: "1499px") {
		@content; } }
@mixin dm {
	@media only screen and (max-width: "1299px") {
		@content; } }
@mixin dt {
	@media only screen and (max-width: "1199px") {
		@content; } }
@mixin ds {
	@media only screen and (max-width: "1023px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }
@mixin xs {
	@media only screen and (max-width: "767px") {
		@content; } }

$white: #fff;
$blue: #325578;
$blue-d: #0c437b;
$blue-bg: #001326;
$blue-lt: #1f71a8;
$blue-l: #f3f7fb;
$orange: #fc6001;
$red: #fc1f01;
$red2-d: #d10b02;
$black: #000;
$sd: 1.5px 2.6px 17.1px 0.9px rgba(26, 71, 117, 0.41);
$sdb: 5px 8.7px 10px rgba(12, 67, 123, 0.5);

body {
	font-family: 'Roboto Condensed', sans-serif;
	font-weight: 400;
	line-height: 1.2;
	font-size: 20px;
	@include lg {
		font-size: 16px; } }

h1,h2,h3,h4,h5,h6 {
	font-weight: 400; }
a {
	text-decoration: none; }

.title-lg {
	color: $white;
	font-size: 44px;
	font-weight: 700;
	line-height: 1;
	@include lg {
		font-size: 33px; }
	@include dt {
		font-size: 28px; }
	@include m {
		font-size: 24px; }
	&--blue {
		color: $blue-d; } }
.title-m {
	color: $white;
	font-size: 44px;
	font-weight: 700;
	line-height: 1;
	@include lg {
		font-size: 34px; }
	@include m {
		font-size: 22px; }
	&--blue {
		color: $blue-d; } }
.title-s {
	color: $white;
	font-size: 30px;
	font-weight: 700;
	line-height: 1.3;
	@include r(1850) {
		font-size: 22px; }
	@include dt {
		font-size: 20px; } }
.logo {
	display: block;
	img {
		width: 191px;
		height: 65px; }
	@include ld {
		img {
			width: 146px;
			height: 50px; } }
	@include dt {
		img {
			width: 100px;
			height: 34px; } } }

.nav {
	font-size: 0;
	text-align: center;
	z-index: 1;
	&__item {
		display: inline-block;
		vertical-align: top;
		position: relative;
		&.active {
			.nav__link {
				color: $white;
				background: $blue-d;
				@include m {
					background: $orange;
					&:after {
						display: none; } } } }
		// &:hover
		// 	+rmin(992)
		// 		.nav__link
		// 			color: $white
		// 			background: $blue-d
		@include m {
			display: block;
			text-align: left;
			position: relative;
			&:last-of-type {
				.nav__link {
					&:after {
						display: none; } } } }
		&--arrow {
			span {
				@include m {
					position: relative;
					&:before {
						content: '';
						position: absolute;
						top: 50%;
						transform: translate(-50%, -50%);
						@include arr(11, 7, $white, b);
						right: -25px; } } } } }
	&__link {
		display: block;
		padding: 5px 15px 15px 15px;
		@include trans;
		font-size: 28px;
		line-height: 1;
		color: $blue;
		@include ld {
			font-size: 24px; }
		@include lg {
			font-size: 18px; }
		@include dt {
			font-size: 16px;
			padding: 5px 12px 11px 12px; }
		@include m {
			padding: 14px 13px;
			position: relative;
			font-size: 22px;
			color: $white;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
				width: 90%;
				height: 1px;
				background: $blue-lt; } }
		@include rmin(992) {
			&:hover {
				color: $white;
				background: $blue-d; } } } }
.loc {
	font-size: 0;
	display: inline-block;
	vertical-align: top;
	margin-top: 8px;
	@include dt {
			margin-top: 2px; }
	&__icon {
		color: $orange;
		font-size: 19px;
		font-weight: 300;
		width: 26px;
		height: 26px;
		line-height: 26px;
		text-align: center;
		border-radius: 50%;
		border: 1px solid $orange;
		display: inline-block;
		vertical-align: top;
		margin-right: 10px;

		@include ld {
			width: 21px;
			height: 21px;
			line-height: 21px;
			font-size: 15px; } }
	span {
		color: $blue;
		font-size: 24px;
		display: inline-block;
		vertical-align: top;
		line-height: 1.1;
		@include ld {
			font-size: 18px; }
		@include dt {
			font-size: 16px; }
		@include dt {
			margin-top: 2px; } } }
.phone {
	color: $blue;
	font-size: 36px;
	font-weight: 400;
	span {
		font-weight: 700; }
	@include ld {
		font-size: 28px; }
	@include dt {
		font-size: 20px; }
	svg {
		display: none;
		fill: $orange;
		@include dt {
			display: inline-block;
			position: relative;
			top: 5px; } }
	@include m {
		font-size: 0;
		svg {
			font-size: 30px;
			top: 0; } } }
.btn, .btn-red {
	border: 1px solid $orange;
	position: relative;
	text-align: center;
	border-radius: 10px;
	padding:  0 5px;
	span {
		position: relative;
		z-index: 1;
		@include trans;
		color: $white; }
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// z-index: -1
		border-radius: 10px;
		background: $white;
		opacity: 0;
		@include trans; }
	&:hover {
		&:after {
			opacity: 1; }
		span {
			color: $orange; } } }
.btn {
	min-width: 294px;
	height: 51px;
	background-color: $orange;
	@include ld {
		min-width: 230px;
		height: 40px; }
	@include dm {
		min-width: 160px;
		height: 28px; }

	span {
		font-size: 24px;
		line-height: 51px;
		@include ld {
			line-height: 40px;
			font-size: 18px; }
		@include dm {
			font-size: 16px;
			line-height: 28px; } } }

.btn-red {
	min-width: 270px;
	display: inline-block;
	vertical-align: top;
	height: 57px;
	border-color: $red;
	background-color: $red;
	@include m {
		min-width: 174px;
		height: 36px; }
	span {
		color: $white;
		font-size: 20px;
		font-weight: 700;
		line-height: 57px;
		@include m {
			line-height: 36px;
			font-size: 13px; } }

	&:hover {
		span {
			color: $red; } } }
.ask-block {
	padding: 10px 15px 32px;
	width: 360px;
	background: $white;
	z-index: 1;
	text-align: center;
	@include m {
		width: 250px;
		margin: 0 auto; }
	&__title {
		color: $red2-d;
		//font-size: 48px
		font-size: 28px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 1;
		margin-bottom: 10px;
		@include m {
			font-size: 18px; } }
	&__sec {
		color: $orange;
		//font-size: 60px
		font-size: 35px;
		font-weight: 700;
		line-height: 1;
		margin-bottom: 60px;
		@include m {
			font-size: 23px;
			margin-bottom: 15px; } }
	&__mob-link {
		display: none;
		@include m {
			display: inline-block;
			vertical-align: top;
			color: $blue-d;
			font-size: 18px;
			font-weight: 700;
			line-height: 1;
			border-bottom: 1px dashed $blue-d;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				@include s(ask-mob-arrow);
				right: -17px;
				top: 50%;
				margin-top: -3px; } } }
	&__top-text {
		color: $blue-d;
		font-size: 28px;
		line-height: 1;
		font-weight: 700;
		margin-bottom: 45px;
		position: relative;
		@include m {
			font-size: 18px;
			margin-bottom: 30px;
			span {
				display: none; } }
		&:after {
			content: '';
			position: absolute;
			bottom: -20px;
			height: 1px;
			background: $blue-d;
			width: 226px;
			left: 50%;
			transform: translateX(-50%);
			@include m {
				bottom: -15px;
				width: 144px; } } }
	&__b-text {
		color: $blue-d;
		font-size: 30px;
		font-weight: 700;
		line-height: 1;
		margin-bottom: 30px;
		@include m {
			font-size: 20px;
			margin-bottom: 15px; } }
	svg {
		fill: $white;
		font-size: 90px;
		position: absolute;
		right: -20px;
		top: 15px; }
	&__btn {
		@include m {
			max-width: 174px; } } }
.callback {
	width: 270px;
	height: 41px;
	line-height: 41px;
	background-color: $blue-d;
	border: 1px solid $white;
	display: block;
	color: $white;
	z-index: 1;
	text-align: center;
	@include trans;
	font-size: 20px;
	@include ld {
		font-size: 16px;
		width: 207px;
		height: 31px;
		line-height: 31px; }
	@include dm {
		width: 191px;
		font-size: 14px; }
	&:hover {
		border-color: $blue-d;
		background: $white;
		color: $blue-d; } }
#quad {
		content: '';
		position: absolute;
		width: 156px;
		height: 142px;
		border: 15px solid $orange;
		z-index: 0;
		@include ld {
			width: 114px;
			height: 104px;
			border: 10px solid $orange; }
		@include dm {
			width: 110px;
			height: 100px; } }
.decor-quad {
	position: relative;
	&:before {
		@extend #quad; }
	&--l-t {
		margin-left: 35px;
		@include ld {
			margin-left: 20px; }
		&:before {
			top: -35px;
			left: -35px;
			@include ld {
				top: -20px;
				left: -20px; } } }
	&--r-t {
		margin-right: 35px;
		@include ld {
			margin-right: 20px; }
		&:before {
			right: -35px;
			top: -35px;
			@include ld {
				right: -20px;
				top: -20px; } } } }
