.slider-block {
	position: relative;
	margin-bottom: 5px;
	margin-top: -2px;
	@include cover;
	@include m {
		background-position: 50% 100%; }

	&__ask-mob {
		display: none;
		@include m {
			display: block; } }
	&__callback {
		position: absolute;
		right: 40px;
		top: 22px;
		@include dt {
			right: 30px; }
		@include m {
			display: none; } }
	&__mouse {
		position: absolute;
		bottom: 12px;
		left: 50%;
		transform: translateX(-50%);
		@include m {
			display: none; }
		svg {
			fill: $blue-d;
			font-size: 67px;
			@include ld {
				font-size: 51px; } } } }

.hslider {
	&--shadow {

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 250px;
			// background: linear-gradient(top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.1) 100%)
			@include m {
				display: none; } } }
	&__slide {
		min-height: 726px;
		padding-top: 203px;
		text-align: right;
		@include ld {
			min-height: 555px;
			padding-top: 154px; }
		@include m {
			padding-top: 0;
			min-height: 100%; }
		.container {
			@include m {
				padding: 0; } } }

	&__content {
		position: relative;
		text-align: center;
		display: inline-block;
		vertical-align: top;
		margin-left: 0;
		margin-right: 15%;
		@include ld {
			margin-left: 0; }

		@include dm {
			margin-left: 0; }
		@include m {
			display: block;
			margin-right: 0;
			margin-bottom: 95px; }
		&:before {
			top: -25px;
			left: -25px;
			@include m {
				display: none; } }
		&:after {
			@extend #quad;
			bottom: -25px;
			right: -25px;
			@include ld {
				bottom: -20px;
				right: -20px;
				@include m {
					display: none; } } } }

	&__inner {
		padding: 22px 10px 15px;
		width: 845px;
		background: $blue-d;
		position: relative;
		z-index: 1;
		@include ld {
			width: 646px;
			padding: 17px 10px 10px; }
		@include dm {
			width: 620px; }
		@include m {
			max-width: 100%;
			width: 100%;
			padding: 10px; } }
	&__title {
		width: auto;
		display: inline-block;
		vertical-align: top;
		padding-bottom: 26px;
		margin-bottom: 29px;
		border-bottom: 1px solid $white;
		color: $white;
		font-size: 34px;
		font-weight: 700;
		line-height: 1.2;
		letter-spacing: 1.5px;
		sub {
			font-size: 20px;
			position: relative;
			top: -10px;
			@include m {
				top: -3px; } }
		@include ld {
			padding-bottom: 16px;
			margin-bottom: 18px; }
		@include m {
			padding-bottom: 12px;
			margin-bottom: 14px;
			font-size: 19px;
			br {
				display: none; } } }
	&__text {
		color: $white;
		line-height: 1;
		font-size: 28px;
		@include ld {
			font-size: 22px; }
		@include m {
			font-size: 15px; }
		span {
			font-size: 24px;
			position: relative;
			left: -4px;
			@include ld {
				font-size: 19px; }
			@include m {
				font-size: 13px; } } }
	&__nav {
		height: 96px;
		border: 10px solid $blue-d;
		position: absolute;
		bottom: -47px;
		right: 104px;
		font-size: 0;
		min-width: 106px;
		z-index: 1;
		padding-top: 9px;
		@include ld {
			height: 74px;
			min-width: 81px;
			padding-top: 0px;
			bottom: -39px; }
		@include m {
			display: none; } }
	&__arrows-block {
		// padding-top: 9px
		// padding-bottom: 5px
		text-align: center;
		position: relative;
		height: 30px;
		@include ld {
			height: 25px;
			margin-top: 3px; }
		&:after {
			content: '';
			position: absolute;
			height: 24px;
			top: 35%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 1px;
			background: $white;
			@include ld {
				height: 19px; } } }
	&__prev, &__next {
		display: inline-block;
		vertical-align: top;
		cursor: pointer;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			left: -5px;
			bottom: -5px;
			right: -5px; }
		svg {
			fill: $white;
			font-size: 24px;
			@include ld {
				font-size: 19px; } } }
	&__prev {
		margin-right: 13px; }
	&__next {
		margin-left: 13px; }

	&__dots {
		height: 37px;
		text-align: center;
		background: $white;
		padding: 7px 10px 0;
		overflow: hidden;
		@include ld {
			height: 28px;
			padding: 6px 10px 0; }
		span {
			&:last-of-type {
				margin-right: 0; } } }
	&__dot {
		height: 15px;
		background: #c2c2c2;
		width: 5px;
		margin-right: 5px;
		display: inline-block;
		vertical-align: bottom;
		cursor: pointer;

		&.is-active {
			height: 23px;
			background: $orange; } } }

